import React from "react"
import {connect} from "react-redux"
import { NavBar, List, InputItem, Grid, Icon } from "antd-mobile"
import { sendMsg,readMsg } from "../../redux/actions"
import QueueAnim from "rc-queue-anim"
import "../../assets/css/index.less"

class Chat extends React.Component{
    state={
        content:"",
        isShown:false
    }

    componentWillMount(){
        const emojis=["😀","😃","😄","😁","😆","😅","🤣","😂","🙂",
            "🙃","😉","😊","😇","🥰","😍","🤩","😘","😗","😚","😙",
            "😋","😛","😜","🤪","😝","🤑","🤗","🤭"]
        this.emoji=emojis.map((emoji)=>({text:emoji}))
    }

    componentDidMount(){
        window.scrollTo(0,document.body.scrollHeight)
    }

    componentDidUpdate(){
        window.scrollTo(0,document.body.scrollHeight)
    }

    componentWillUnmount(){
        const targetId=this.props.match.params.id
        const userid=this.props.user._id
        this.props.readMsg(targetId,userid)
    }

    toggleShow=()=>{
        let isShown=this.state.isShown
        isShown=!isShown
        this.setState({isShown})
        if(isShown){
            setTimeout(()=>{
                window.dispatchEvent(new Event("resize"))
            },0)
        }
    }

    handleSend=()=>{
        const from=this.props.user._id
        const to=this.props.match.params.id
        const content=this.state.content
        if(content){
            this.props.sendMsg({from,to,content})
        }
        this.setState({content:"",isShown:false})

    }
    
    render(){
        const {users,chatMsgs}=this.props.msglist
        const meId=this.props.user._id
        if(!users[meId]){
            return null
        }
        const targetIcon=require(`../../assets/icons/${users[this.props.match.params.id].header}.png`)
        const chatId=[this.props.user._id,this.props.match.params.id].sort().join("_")
        const msgs=chatMsgs.filter((msg)=>(msg.chat_id===chatId))
        return(
            <div id="chat-page">
                <NavBar 
                className="sticky-header" 
                icon={<Icon type="left"/>} 
                onLeftClick={()=>{this.props.history.replace("/message")}}>
                    {users[this.props.match.params.id].username}
                </NavBar>
                <List style={{marginTop:50,marginBottom:50}}>
                    {msgs.map((msg)=>{
                        if(msg.from===this.props.match.params.id){
                            return <List.Item type="left" className="chat-other" key={msg._id} thumb={targetIcon}>{msg.content}</List.Item>
                        }else{
                            return <List.Item type="right" className="chat-me" key={msg._id} extra="Me">{msg.content}</List.Item>
                        }
                    })}
                </List>
        <InputItem 
        //className="am-tab-bar" 
        className="my-input-bar"
        placeholder="please enter your message" 
        value={this.state.content}
        onFocus={()=>{this.setState({isShown:false})}}
        onChange={(val)=>{this.setState({"content":val})}}
        extra={<span>
            <span onClick={()=>{this.toggleShow()}}>😀</span>
<span style={{marginRight:10}} onClick={()=>{this.handleSend()}}>send</span>
        </span>}>
        </InputItem> 
        {this.state.isShown?(<Grid 
        data={this.emoji} 
        columnNum={8} 
        onClick={(emoji)=>{this.setState({content:this.state.content+emoji.text})}}/>):null}
            </div>
        )
    }
}

export default connect(
    state=>({user:state.user,msglist:state.msglist}),
    {sendMsg,readMsg}
)(Chat)