import React from "react"
import Logo from "../../components/logo/logo"
import {NavBar, WhiteSpace, WingBlank, InputItem,List,Button} from "antd-mobile"
import {connect} from "react-redux"
import {login} from "../../redux/actions"
import { Redirect } from "react-router-dom"
import "../../assets/css/index.less"

class Login extends React.Component{
    state={
        username:"",
        password:"",
    }

    handleChange(name,value){
        this.setState({
            [name]:value
        })
    }

    login(){
        this.props.login(this.state)
    }

    toRegister(){
        this.props.history.replace("/register")
    }
    
    render(){
        const {msg, redirect} = this.props.user
        
        if(redirect){
            return <Redirect to={redirect} />
        }
        return(
            <div>
                <NavBar>C&nbsp;A&nbsp;R&nbsp;E&nbsp;E&nbsp;R&nbsp;&nbsp;U&nbsp;P&nbsp;</NavBar>
                <WhiteSpace />
                <Logo />
                <WhiteSpace />
                <WingBlank>
                    {msg?<div className="error-msg">{msg}</div>:null}
                    <List>
                        <InputItem onChange={(val)=>{this.handleChange("username",val)}}>Username:&nbsp;</InputItem>
                        <InputItem type="password" onChange={(val)=>{this.handleChange("password",val)}}>Password:&nbsp;</InputItem>
                    </List>
                    <WhiteSpace />
                    <Button type="primary" onClick={()=>{this.login()}}>Login</Button>
                    <Button onClick={()=>{this.toRegister()}}>don't have an account</Button>
                </WingBlank>
            </div>
        )
    }
}

export default connect(state => ({user:state.user}),{login})(Login)