import React from "react"
import {List, Grid} from "antd-mobile"
import PropTypes from "prop-types"

export default class IconSelector extends React.Component{
    static propTypes={
        selectAvatar: PropTypes.func.isRequired
    }

    state={
        icon:null
    }

    constructor(props){
        super(props)
        this.icons=[]
        const iconList=["man","woman","girl","crab","chick","hedgehog","hippo","koala","lemur","pig","tiger","whale","zebra"]
        for(let i=0;i<iconList.length;i++){
            this.icons.push({
                text: iconList[i],
                icon:require(`../../assets/icons/${iconList[i]}.png`)
            })
        }
    }

    selectIcon({text,icon}){
        this.setState({icon})
        this.props.selectAvatar(text)
    }

    render(){
        const header = this.state.icon?
        <div>Icon Selected: <img alt="icon" src={this.state.icon} /></div>:
        <div>Please select an avatar</div>
        return(
            <List renderHeader={()=>header}>
                <Grid data={this.icons} columnNum={5} onClick={(e)=>this.selectIcon(e)}></Grid>
            </List>
        )
    }
}