import React from "react"
import {connect} from "react-redux"
import {Result, List, Button, WhiteSpace, Modal} from "antd-mobile"
import {resetUser} from "../../redux/actions"
import Cookies from "js-cookie"

class Personal extends React.Component{
    handleClick(){
        Modal.alert("EXIT","Are your sure?",[
            {text:"Cancel"},
            {text:"OK",onPress:()=>{
                Cookies.remove("userid")
                this.props.resetUser()}}
        ])
    }
    
    render(){
        const {username,header,company,salary,info,post}=this.props.user
        const {Item}=List
        const {Brief}=Item
        return(
            <div id="personal" style={{marginTop:50,marginBottom:50}}>
                <Result img={<img alt="icon" src={require(`../../assets/icons/${header}.png`)} />}
                    title={username}
                    message={company} />
                <List>
                    <Item multipleLine>
                        <Brief>Position: {post}</Brief>
                        <Brief>Package: {salary}</Brief>
                        <Brief>Qualification: {info}</Brief>
                    </Item>
                </List>
                <WhiteSpace />
                <Button type="warning" onClick={()=>{this.handleClick()}}>Exit</Button>
            </div>
        )
    }
}

export default connect(
    state=>({user:state.user}),
    {resetUser}
)(Personal)