import React from "react"
import {connect} from "react-redux"
import { List, Badge } from "antd-mobile"
import QueueAnim from "rc-queue-anim"

function getMsgList(chatMsgs,userid){
    let result={}
    chatMsgs.map((chatMsg)=>{
        if(!chatMsg.read&&chatMsg.to===userid){
            chatMsg.unread=1
        }else{
            chatMsg.unread=0
        }

        const tmp=result[chatMsg.chat_id]
        if(tmp){
            const tmp_unread=tmp.unread
            if(tmp.create_time<chatMsg.create_time){
                result[chatMsg.chat_id]=chatMsg
            }
            result[chatMsg.chat_id].unread=tmp_unread+chatMsg.unread
        }else{
            result[chatMsg.chat_id]=chatMsg
        }
    })
    let lastMsgs=Object.values(result)
    lastMsgs.sort(function(m1,m2){
        return m2.create_time-m1.create_time
    })
    
    return lastMsgs
}

class Message extends React.Component{

    render(){
        const {users,chatMsgs}=this.props.msglist
        const {user}=this.props
        const lastMsgs = getMsgList(chatMsgs,user._id)
        return(
            <div id="message" style={{marginTop:50,marginBottom:50}}>
                
                <List>
                    <QueueAnim type="left" delay={100}>
                    {lastMsgs.map((msg)=>{
                        const curId=user._id===msg.from?msg.to:msg.from
                        const curUsername=users[curId].username
                        const curHeader=users[curId].header
                        return(
                            <List.Item
                                key={msg._id} 
                                extra={<Badge text={msg.unread}/>}
                                thumb={curHeader?require(`../../assets/icons/${curHeader}.png`):null}
                                arrow="horizontal"
                                onClick={()=>{this.props.history.replace(`/chat/${curId}`)}}
                            >
                                {msg.content}
                                <List.Item.Brief>
                                    {curUsername}
                                </List.Item.Brief>
                            </List.Item>
                        )
                    })}
                    </QueueAnim>
                </List>
                
            </div>
        )
    }
}

export default connect(
    state=>({
        msglist:state.msglist,
        user:state.user})
)(Message)