import ajax from "./ajax"

export const reqRegister=(user)=>{
    return ajax("/register","POST",user)
}

export const reqLogin=(user)=>{
    return ajax("/login","POST",user)
}

export const reqUpdate=(user)=>{
    return ajax("/update","POST",user)
}

export const reqUser=()=>{
    return ajax("/user")
}

export const reqUserlist=(type)=>{
    return ajax("/userlist","GET",{type})
}

export const reqChatList=()=>{
    return ajax("/chatlist")
}

export const reqReadMsg=(from)=>{
    return ajax("/readmsg","POST",{from})
}