import React from "react"
import Logo from "../../components/logo/logo"
import {NavBar, WhiteSpace, WingBlank, InputItem,List,Radio,Button} from "antd-mobile"
import {connect} from "react-redux"
import {register} from "../../redux/actions"
import {Redirect} from "react-router-dom"
import "../../assets/css/index.less"

class Register extends React.Component{
    state={
        username:"",
        password:"",
        password2:"",
        type:"recruiter"
    }

    handleChange(name,value){
        this.setState({
            [name]:value
        })
    }

    register(){
        this.props.register(this.state)
    }

    toLogin(){
        this.props.history.replace("/login")
    }
    
    render(){
        const {type}=this.state
        const {msg, redirect}=this.props.user
        if(redirect)
            return <Redirect to={redirect} />
        
        return(
            <div>
                <NavBar>C&nbsp;A&nbsp;R&nbsp;E&nbsp;E&nbsp;R&nbsp;&nbsp;U&nbsp;P&nbsp;</NavBar>
                <WhiteSpace />
                <Logo />
                <WhiteSpace />
                <WingBlank>
                    <List>
                        {msg?<div className="error-msg">{msg}</div>:null}
                        <InputItem onChange={(val)=>{this.handleChange("username",val)}}>Username:&nbsp;</InputItem>
                        <InputItem type="password" onChange={(val)=>{this.handleChange("password",val)}}>Password:&nbsp;</InputItem>
                        <InputItem type="password" onChange={(val)=>{this.handleChange("password2",val)}}>Confirm:&nbsp;</InputItem>
                        <List.Item>
                            <span>I am a:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                            <Radio checked={type==="candidate"} onClick={()=>{this.handleChange("type","candidate")}}>candidate&nbsp;&nbsp;&nbsp;&nbsp;</Radio>
                            <Radio checked={type==="recruiter"} onClick={()=>{this.handleChange("type","recruiter")}}>recruiter</Radio>
                        </List.Item>
                    </List>
                    <WhiteSpace />
                    <Button type="primary" onClick={()=>{this.register()}}>REGISTER</Button>
                    <Button onClick={()=>{this.toLogin()}}>already have an account</Button>
                </WingBlank>
            </div>
        )
    }
}

export default connect(state=>({user:state.user}),{register})(Register)