import { combineReducers } from "redux"
import {AUTH_SUCCESS,ERROR_MSG, RECEIVE_USER, RESET_USER, RECEIVE_USERLIST, RECEIVE_MSGLIST, RECEIVE_MSG, READ_MSG} from "./action-types"
import {getRedirectTo} from "../utils"

const initState={
    username:"",
    type:"",
    msg:"",
    redirect:""
}

const initUserlist=[]

function user(state=initState, action){
    switch(action.type){
        case AUTH_SUCCESS:
            const {type,header}=action.data
            return {...action.data,redirect:getRedirectTo(type,header)}
        case ERROR_MSG:
            return {...state,msg:action.data}
        case RECEIVE_USER:
            return action.data
        case RESET_USER:
            return {...initState,msg:action.data}
        default:
            return state
    }
}

function userlist(state=initUserlist,action){
    switch(action.type){
        case RECEIVE_USERLIST:
            return action.data
        default:
            return state
    }
}

const initMsgs={
    "users":{},
    "chatMsgs":[],
    "unread":0
}

function msglist(state=initMsgs,action){
    switch(action.type){
        case RECEIVE_MSGLIST:
            const {users,chatMsgs,userid}=action.data
            let tmp_unread=0
            chatMsgs.forEach((chatMsg)=>{
                if(chatMsg.to===userid&&!chatMsg.read){
                    tmp_unread=tmp_unread+1
                }
            })
            return {users,chatMsgs,unread:tmp_unread}
        case RECEIVE_MSG:
            const {chatMsg}=action.data
            return {
                users:state.users,
                chatMsgs:[...state.chatMsgs,chatMsg],
                read:state.unread+(!chatMsg.unread&&chatMsg.to===action.data.userid?1:0)
            }
        case READ_MSG:
            const {from,to,read}=action.data
            return {
                users:state.users,
                chatMsgs:state.chatMsgs.map((chatMsg)=>{
                    if(chatMsg.to===to&&chatMsg.from===from&&!chatMsg.read){
                        return {...chatMsg,read:true}
                    }
                    else{
                        return chatMsg
                    }
                }),
                unread:state.unread-read
            }
        default:
            return state
    }
}

export default combineReducers({
    user,
    userlist,
    msglist
})