import React from "react"
import {Route, Switch, Redirect} from "react-router-dom"
import RecruiterInfo from "../recruiter-info/recruiter-info"
import CandidateInfo from "../candidate-info/candidate-info"
import {connect} from "react-redux"
import Cookies from "js-cookie"
import {getRedirectTo} from "../../utils"
import {getUser} from "../../redux/actions"
import {NavBar} from "antd-mobile"
import Recruiter from "../recruiter/recruiter"
import Candidate from "../candidate/candidate"
import Message from "../message/message"
import Personal from "../personal/personal"
import Chat from "../chat/chat"
import NavFooter from "../../components/nav-footer/nav-footer"
import "../../assets/css/index.less"

class Main extends React.Component{
    navList=[
        {
            path:"/recruiter",
            component:Recruiter,
            title:"CANDIDATE LIST",
            icon:"candidate",
            text:"candidate",
            hide:false
        },
        {
            path:"/candidate",
            component:Candidate,
            title:"RECRUITER LIST",
            icon:"recruiter",
            text:"recruiter",
            hide:false
        },
        {
            path:"/message",
            component:Message,
            title:"MESSAGE LIST",
            icon:"message",
            text:"message"
        },
        {
            path:"/personal",
            component:Personal,
            title:"USER CENTER",
            icon:"personal",
            text:"profile"
        }
    ]
    
    componentDidMount(){
        const {_id}=this.props.user
        const userid=Cookies.get("userid")
        if(userid && !_id){
            this.props.getUser()
        }
    }
    
    render(){
        const userid = Cookies.get("userid")
        if(!userid){
            return <Redirect to="/login" />
        }
        if(!this.props.user._id){
            return null
        }else{
            if(this.props.location.pathname==="/"){
                const {type,header}=this.props.user
                const path=getRedirectTo(type,header)
                return <Redirect to={path} />
            }
        }
        
        let {navList}=this
        const curNav=navList.find((nav)=>(this.props.location.pathname===nav.path))
        if(curNav){
            this.props.user.type==="recruiter"?navList[1].hide=true:navList[0].hide=true
            navList=navList.filter((nav)=>(!nav.hide))
        }

        return (
            <div>
                {curNav?<NavBar className="sticky-header">{curNav.title}</NavBar>:null}
                <Switch>
                    {navList.map((nav)=><Route path={nav.path} component={nav.component} />)}
                    <Route path="/recruiter-info" component={RecruiterInfo}></Route>
                    <Route path="/candidate-info" component={CandidateInfo}></Route> 
                    <Route path="/chat/:id" component={Chat}></Route>
                </Switch>
                {curNav?<NavFooter navList={navList} unread={this.props.unread}></NavFooter>:null}
            </div>
        )
    }
}

export default connect(
    state=>({user:state.user,unread:state.msglist.unread}),
    {getUser}
)(Main)