import React from "react"
import {NavBar, InputItem, Button, TextareaItem, WhiteSpace} from "antd-mobile"
import IconSelector from "../../components/icon-selector/icon-selector"
import {connect} from "react-redux"
import {update} from "../../redux/actions"
import {Redirect} from "react-router-dom"

class RecruiterInfo extends React.Component{
    state={
        company:"",
        post:"",
        salary:"",
        info:"",
        header:""
    }
    
    handleChange(name,val){
        this.setState({
            [name]:val
        })
    }

    selectAvatar=(icon)=>{
        this.setState({
           header:icon
        })
    }

    save(){
        this.props.update(this.state)
    }

    render(){
        const {header,type}=this.props.user
        if(header){
            const path = type==="recruiter"?"/recruiter":"candidate"
            return <Redirect to={path} />
        }

        return(
            <div id="recruiter-info">
                <NavBar>COMPLETE PROFILE</NavBar>
                <IconSelector selectAvatar={this.selectAvatar}/>
                <InputItem onChange={(val)=>{this.handleChange("company",val)}}>Company: </InputItem>
                <InputItem onChange={(val)=>{this.handleChange("post",val)}}>Position: </InputItem>
                <InputItem onChange={(val)=>{this.handleChange("salary",val)}}>Package: </InputItem>
                <TextareaItem title="Info: " onChange={(val)=>{this.handleChange("info",val)}}></TextareaItem>
                <WhiteSpace />
                <Button type="primary" onClick={()=>{this.save()}}>SAVE</Button>
            </div>
        )
    }
}

export default connect(
    state=>({user:state.user}),
    {update}
)(RecruiterInfo)