import {reqRegister,reqLogin,reqUpdate, reqUser, reqUserlist, reqChatList, reqReadMsg} from "../api/index"
import { AUTH_SUCCESS, ERROR_MSG, RECEIVE_USER, RESET_USER, RECEIVE_USERLIST, RECEIVE_MSGLIST, RECEIVE_MSG, READ_MSG } from "./action-types"
import io from "socket.io-client"

const initIo=(dispatch,userid)=>{
    if(!io.socket){
        io.socket=io("https://mighty-coast-98531.herokuapp.com/"||"ws://localhost:8080")
    }
    console.log("run!!")
    io.socket.removeAllListeners()
    io.socket.on("receiveMsg",(data)=>{
        console.log("run!")
        console.log("receive message from server",data)
        if(data.from==userid||data.to==userid){
            dispatch(getMessage(data,userid))
        }
    })
}

const authSuccess=(userData)=>{
    return {type:AUTH_SUCCESS,data:userData}
}

const errorMessage=(msg)=>{
    return {type:ERROR_MSG,data:msg}
}

const receiveUser=(userData)=>{
    return {type:RECEIVE_USER,data:userData}
}

const receiveUserlist=(userlist)=>{
    return {type:RECEIVE_USERLIST,data:userlist}
}

export const resetUser=(msg)=>{
    return {type:RESET_USER,data:msg}
}

const getMessageList=({users,chatMsgs,userid})=>{
    return {type:RECEIVE_MSGLIST,data:{users,chatMsgs,userid}}
}

const getMessage=(chatMsg,userid)=>{
    return {type:RECEIVE_MSG,data:{chatMsg,userid}}
}

const readMessage=(from,to,read)=>{
    return {type:READ_MSG,data:{from,to,read}}
}

export const register=(userData)=>{
    
    const {username,password,password2}=userData
    
    if(!username){
        return errorMessage("Please input username!")
    }else if(!password){
        return errorMessage("Please input password!")
    }else if(password!==password2){
        return errorMessage("Inconsistent passwords!")
    }

    return async dispatch =>{
        const response = await reqRegister(userData)
        const result=response.data
        if(result.code===0){
           getMsgList(dispatch,result.data._id)
           dispatch(authSuccess(result.data))
        }else{
           dispatch(errorMessage(result.msg))
        }
    }
}

export const login=(userData)=>{
    
    const {username,password}=userData
    if(!username){
        return errorMessage("Please input username!")
    }else if(!password){
        return errorMessage("Please input password!")
    }

    return async dispatch=>{
        const response = await reqLogin({username,password})
        const result=response.data
        if(result.code===0){
           getMsgList(dispatch,result.data._id)
           dispatch(authSuccess(result.data))
        }else{
           dispatch(errorMessage(result.msg))
        }
    }
}

export const update=(userData)=>{
    return async dispatch=>{
        const response=await reqUpdate(userData)
        const result=response.data
        if(result.code===0){
            dispatch(receiveUser(result.data))
        }else{
            dispatch(resetUser(result.msg))
        }
    }
}

export const getUser=()=>{
    return async dispatch=>{
        const response=await reqUser()
        const result=response.data
        if(result.code===0){
            getMsgList(dispatch,result.data._id)
            dispatch(receiveUser(result.data))
        }else{
            dispatch(resetUser(result.msg))
        }
    }
}

export const getUserlist=(type)=>{
    return async dispatch=>{
        const response=await reqUserlist(type)
        const result=response.data
        if(result.code===0){
            dispatch(receiveUserlist(result.data))
        }
    }
}

async function getMsgList(dispatch,userid){
    initIo(dispatch,userid)
    const response=await reqChatList()
    const result=response.data
    if(result.code===0){
        const {users,chatMsgs}=result.data
        dispatch(getMessageList({users,chatMsgs,userid}))
    }
}

export const sendMsg=(data)=>{
    return dispatch=>{
        io.socket.emit("sendmsg",data)
        console.log("send message to server",data)
    }
}

export const readMsg=(from,userid)=>{
    return async dispatch=>{
        const response=await reqReadMsg(from)
        const to=userid
        const result=response.data
        if(result.code===0){
            dispatch(readMessage(from,to,result.data))
        }
    }
}