import React from "react"
import {connect} from "react-redux"
import { WhiteSpace,Card, WingBlank } from "antd-mobile"
import {getUserlist} from "../../redux/actions"
import {withRouter} from "react-router-dom"
import QueueAnim from "rc-queue-anim"

class UserList extends React.Component{
    componentDidMount(){
        this.props.getUserlist(this.props.type)
    }
    
    render(){
        const {userlist}=this.props
        return(
            <div>
                <WingBlank style={{marginBottom:50,marginTop:50}}>
                <QueueAnim type="scale">
                {userlist.map((user)=>(
                    <div key={user._id}>
                        <WhiteSpace />
                        <Card onClick={()=>{this.props.history.push(`/chat/${user._id}`)}}>
                            <Card.Header 
                            thumb={user.header?require(`../../assets/icons/${user.header}.png`):null}
                            extra={user.username} />
                            <Card.Body>
                                <div>Position: {user.post}</div>
                                {user.company?<div>Company: {user.company}</div>:null}
                                {user.package?<div>Package: {user.pachage}</div>:null}
                                <div>Info: {user.info}</div>
                            </Card.Body>
                        </Card>
                    </div>
                ))}
                </QueueAnim>
                </WingBlank>
            </div>
        )
    }
}

export default withRouter(connect(
    state=>({userlist:state.userlist}),
    {getUserlist}
)(UserList))